.CAB_DK_carroussel_wrap {
    /* background-color: grey; */
    background-color: rgba(255, 255, 255, 0.45);
    /* background-color: red; */
    /* background-color: black; */
    width: 100%;
    /* max-width: 800px; */
    margin: auto;
    /* height: 30%; */
    height: 350px;
}

/* carroussel css */

.CAB_DK_carrousel_container {
    /* background-color: rgb(33, 211, 57); */
    height: 100%;
    width: 87.5%;
    overflow: hidden;
    /* margin: 0 0 15px 0; */
    margin: auto;
    padding: 0px;
}



.CAB_DK_carrousel_part {
    width: 33.33333%;
    height: 100%;
    display: inline-block;
    background-size: cover;
    background-position: center;
    /* background-color: white; */
    /* background-color: red; */
}

.CAB_DK_carrousel_slideimage_wrap {
    /* background-color: yellow; */
    height: 100%;
    /* height: 0; */
    width: 100%;
    margin: auto;
    /* object-fit: cover; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.teste44 {
    /* background-color: orange; */
    height: 100%;
    /* height: 0; */
    width: 100%;
    /* object-fit: contain; */
}

.CAB_DK_carrousel_slideimage {
    /* background-color: aqua; */
    width: 90%;
    height: 90%;
    object-fit: cover;
    opacity: 0.6;
    transition: opacity 0.5s ease-in-out;
}

.CAB_DK_carrousel_slideimage_active {
    /* background-color: red; */
    width: 90%;
    height: 90%;
    object-fit: cover;
    /* color: red; */
    /* border: solid black 2px; */
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.CAB_DK_btn_wrap {
    /* background-color: aqua; */
    height: 0;
    /* height: 50px; */
    width: 95%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    /* border: 40px red solid; */
}

.CAB_DK_btn {
    background-color: white;
    /* background-color: orange; */
    border: none;
    font-size: 2.4em;
    height: 50px;
    position: relative;
    top: -200px;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;
    z-index: 1;
    cursor: pointer;

    transition: all 0.5s ease-out;
    background-color: transparent;
    color: transparent;
}

.CAB_DK_carroussel_wrap:hover .CAB_DK_btn {
    transition: all 0.1s ease-out;
    background-color: white;
    color: black;
    /* height: 0px; */
    /* border: 80px red solid; */
}

