.FCRC_wrap {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  background-color: rgb(219, 219, 219);
}


.FCRD_card_wrap {
  /* background-color: aqua; */
  background-color: rgb(219, 219, 219);
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  /* border: 1px solid black; */
}

.FCRD_card_img_wrap {
  background-color: white;
  height: 90px;
  width: 90px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.FCRD_card_txt_wrap {
  /* width: 90%; */
  width: calc(100% - 110px);
  /* background-color: red; */
}

.FCRD_card_txt_titre {
  display: flex;
}

.FCRD_card_acheteur {
  margin: 0;
  padding: 0;
  margin-right: 8px;
}

.FCRD_card_stars {
  display: flex;
  align-items: center;

}

.FCRD_icc {
  margin: 0 3px;
  /* background-color: yellow; */
  font-size: 1.0em;
  /* color: rgb(161, 161, 161); */
}

.FCRD_icc_grey {
  margin: 0 3px;
  /* background-color: yellow; */
  font-size: 1.0em;
  color: rgb(161, 161, 161);
}

.FCRD_card_date {
  margin: 0;
  color: grey;
}

.FCRD_card_txt {
  margin: 0;
}


.false_pic {
  height: 40px;
  width: 40px;
  background-color: blue;
}