.FPRC_DK_carroussel_wrap {
  /* background-color: grey; */
  background-color: rgba(255, 255, 255, 0.45);
  /* background-color: black; */
  width: 100%;
  /* max-width: 800px; */
  margin: auto;
  /* height: 30%; */
  height: 350px;
}

/* carroussel css */

.FPRC_DK_carrousel_container {
  /* background-color: rgb(33, 211, 57); */
  height: 100%;
  width: 100%;
  overflow: hidden;
  /* margin: 0 0 15px 0; */
  /* margin: auto; */
  padding: 0px;
}



.FPRC_DK_carrousel_part {
  width: 100%;
  height: 100%;
  display: inline-block;
  background-size: cover;
  background-position: center;
  /* background-color: white; */
  /* background-color: red; */
}

.FPRC_DK_carrousel_slideimage_wrap {
  /* background-color: yellow; */
  height: 90%;
  /* height: 0; */
  width: 100%;
  /* margin: auto; */
  /* object-fit: cover; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.FPRC_DK_carrousel_slideimage {
  /* background-color: aqua; */
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* opacity: 0.6; */
  /* transition: opacity 0.5s ease-in-out; */
}

.FPRC_DK_carrousel_txt_wrap {
  background-color: rgb(153, 153, 153);
  height: 10%;
  /* height: 0; */
  width: 100%;
  /* margin: auto; */
  /* object-fit: cover; */
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.FPRC_DK_carrousel_txt {
  /* background-color: aqua; */
  margin: 0;
  padding: 0;
  padding-right: 10px;
}

.FPRC_DK_carrousel_slideimage_active {
  /* background-color: red; */
  width: 90%;
  height: 90%;
  object-fit: cover;
  /* color: red; */
  /* border: solid black 2px; */
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.FPRC_DK_btn_wrap {
  /* background-color: aqua; */
  height: 0;
  /* height: 10%; */
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  /* border: 40px red solid; */
}

.FPRC_DK_btn {
  background-color: white;
  background-color: orange;
  border: none;
  font-size: 2.4em;
  height: 50px;
  position: relative;
  top: -200px;
  border-radius: 10px;
  display: flex;
  align-items: flex-end;
  z-index: 1;
  cursor: pointer;

  transition: all 0.5s ease-out;
  background-color: transparent;
  color: transparent;
}

.ACC_DK_RC_content:hover .FPRC_DK_btn {
  transition: all 0.1s ease-out;
  background-color: white;
  color: black;
  /* height: 0px; */
  /* border: 80px red solid; */
}

