.ACC_DK_commande_form {
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    /* background-color: aqua; */
}

.ACC_DK_commande_form_label_wrap {
    display: flex;
    background-color: white;
    /* background-color: transparent; */
    /* background-color: orchid; */
    color: black;
    border: 1px solid rgb(172, 172, 172);
    border: 1px solid white;
    outline: 1px solid white;
    margin: 10px 0;
    cursor: text;
    width: 48%;

    display: flex;
    justify-content: space-between;

    box-shadow: 0px 2px 10px rgb(0, 0, 0);
    /* transition: 2s all ease; */
}

.ACC_DK_commande_form_label_wrap_error {
    display: flex;
    background-color: rgb(255, 255, 255);

    /* background-color: transparent; */
    /* background-color: orchid; */
    color: black;
    border: 1px solid rgb(172, 172, 172);
    border: 1px solid white;
    outline: 1px solid white;
    margin: 10px 0;
    cursor: text;
    width: 48%;

    display: flex;
    justify-content: space-between;

    display: flex;
    justify-content: space-between;

    animation: fadeInFromNone 0.2s linear;
    
    box-shadow: 0px 2px 10px rgb(0, 0, 0);
}


.ACC_DK_commande_form_label_wrap_2 {
    display: flex;
    background-color: white;
    /* background-color: transparent; */
    /* background-color: orchid; */
    color: black;
    border: 1px solid rgb(172, 172, 172);
    border: 1px solid white;
    outline: 1px solid white;
    margin: 10px 0;
    cursor: text;
    width: 100%;

    display: flex;
    justify-content: space-between;

    /* transition: 2s all ease; */
    box-shadow: 0px 2px 10px rgb(0, 0, 0);
}

.ACC_DK_commande_form_label_wrap_2_error {
    display: flex;
    background-color: white;
    /* opacity: 0.5; */
    /* background-color: transparent; */
    /* background-color: orchid; */
    color: black;
    border: 1px solid rgb(172, 172, 172);
    border: 1px solid white;
    outline: 1px solid white;
    margin: 10px 0;
    cursor: text;
    width: 100%;

    display: flex;
    justify-content: space-between;

    animation: clignote 0.2s ease-in-out;
    
    box-shadow: 0px 2px 10px rgb(0, 0, 0);
}

@keyframes clignote {
    25% {
        opacity: 0.3;
    }

    50% {
        opacity: 1;
    }

    75% {
        opacity: 0.3;
    }

    100% {
        opacity: 1;
    }
}

.ACC_DK_commande_form_label_wrap_red {
    display: flex;
    background-color: white;
    color: black;
    border: 1px solid red;
    outline: 1px solid red;
    margin: 15px 0;
    cursor: text;
    width: 48%;
}

.ACC_DK_commande_form_label_wrap_green {
    display: flex;
    background-color: white;
    color: black;
    border: 1px solid green;
    outline: 1px solid green;
    margin: 15px 0;
    cursor: text;
    width: 48%;
}

.ACC_DK_commande_form_label_wrap:focus-within {
    display: flex;
    background-color: white;
    color: black;
    border: 1px solid rgb(172, 172, 172);
    border: 1px solid black;
    outline: 1px solid white;
    width: 48%;
}

.ACC_DK_commande_form_txt {
    /* background-color: cadetblue; */
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: calc(100% - 15px);
}

.ACC_DK_commande_form_label_text {
    margin: 0;
    padding: 5px;
    /* width: 30%; */
    width: 130px;
}

.ACC_DK_error_green {
    width: 15px;
    margin: 0;
    padding: 5px;
    color: green;
    color: black;
    font-weight: bold;
    text-align: center;

    transition: all 1s ease;
}

.ACC_DK_error_unknown {
    width: 15px;
    margin: 0;
    padding: 5px;
    /* background-color: aqua; */

    transition: all 1s ease;
}

.ACC_DK_error_red {
    width: 15px;
    margin: 0;
    padding: 5px;
    color: red;
    color: black;
    font-weight: bold;
    /* background-color: aqua; */
    text-align: center;

    /* transform: translateY(10px); */

}

.ACC_DK_commande_form_textarea {
    background-color: transparent;
    /* background-color: rgb(88, 88, 88); */
    width: 70%;
    /* width: auto; */
    height: auto;
    color: black;
    /* border: 1px solid rgb(172, 172, 172); */
    border: none;
    appearance: none;
}

textarea.ACC_DK_commande_form_textarea {
    height: 150px;
}

.ACC_DK_commande_form_textarea:focus {
    /* background-color: white; */
    /* color: white; */
    /* border: 1px solid rgb(172, 172, 172); */
    /* border: 2px solid red; */
    outline: none;
    background-color: rgb(43, 43, 43);
    background-color: rgb(235, 235, 235);
    background-color: white;
}

.ACC_DK_commande_form_input {
    background-color: transparent;
    /* background-color: rgb(88, 88, 88); */
    width: 45%;
    height: auto;
    width: calc(100% - 130px);
    color: black;
    /* border: 1px solid rgb(172, 172, 172); */
    border: none;
    appearance: none;
}

textarea.ACC_DK_commande_form_input {
    height: 100px;
}

.ACC_DK_commande_form_input:focus {
    /* background-color: white; */
    /* color: white; */
    /* border: 1px solid rgb(172, 172, 172); */
    /* border: 2px solid red; */
    outline: none;
    background-color: rgb(43, 43, 43);
    background-color: rgb(235, 235, 235);
    background-color: white;
}


