.CPB_DK_top_link {
    /* background-color: aqua; */
    /* color: white; */
    display: flex;
    justify-content: space-around;
    margin-left: min(20%, 20px);
    padding-top: 0px;
    width: 60%;
    max-width: 280px;
}

.CPB_DK_top_link_txt {
    color: blue;
    color: white;
    text-decoration: none;
    font-size: 1.3em;
    /* margin: 0 10px; */
    /* background-color: red; */
}

p.CPB_DK_top_link_txt {
    color: black;
    color: white;
}

.CPB_DK_product_wrap {
    /* background-color: red; */
    width: 80%;
    height: 600px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.CPB_DK_partie_gauche {
    /* background-color: orange; */
    width: 45%;
    height: 100%;
}

.CPB_DK_partie_droite {
    /* background-color: white; */
    /* color: black; */
    width: 50%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* border: black 5px solid; */
    /* background-color: goldenrod; */
}

.CPB_DK_partie_droite_description {
    background-color: white;
    color: black;
    width: 100%;
    height: 70%;
}

.CPB_DK_partie_droite_description p {
    padding: 5px 10px;
    margin: 0;
}

.CPB_DK_partie_droite_description h2 {
    padding: 10px 20px;
    margin: 0;
}

.CPB_DK_partie_droite_chariot {
    /* background-color: white; */
    /* color: black; */
    width: 100%;
    height: 25%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.CPB_DK_partie_droite_chariot_btn {
    background-color: black;
    transition:
        /* background-color 0.6s ease-in, */
        /* color 0.6s ease-in, */
        /* border 0.6s ease-in; */
        all 0.6s ease-in;
    color: white;
    border: 2px solid black;
    /* border-radius: 5px; */
    display: flex;
    align-items: center;
    width: 80%;
    height: 40%;
    min-width: 280px;
    text-align: center;
    display: flex;
    justify-content: center;
    margin: auto;
    cursor: pointer;
    margin: 0 auto;
}

.CPB_DK_partie_droite_chariot_btn:hover {
    /* background-color: white; */
    /* transition: all 0.6s ease-in; */
    transition:
        background-color 0.6s ease-in,
        color 0.6s ease-in,
        border 0.2s ease-out;
    /* color: black;  */
    border: 2px solid white;
}

.CPB_DK_partie_droite_chariot_btn:active {
    background-color: white;
    transition: all 0.05s ease-out;
    color: black;
    border: 2px solid white;
}

.CPB_DK_partie_droite_chariot_btn:active:hover {
    background-color: white;
    transition: all 0.05s ease-out;
    color: black;
    border: 2px solid white;
}

.CPB_DK_partie_droite_chariot_btn_txt {
    font-size: 1.4em;
    padding: 0;
    margin: 0;
}

.CPB_DK_partie_droite_chariot_explication_wrap {
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    height: 40%;
    min-width: 280px;
    text-align: center;
    display: flex;
    justify-content: center;
    margin: auto;
    margin: 0 auto;
}

.CPB_DK_partie_droite_chariot_explication_txt {
    font-size: 1em;
    padding: 0;
    margin: 0;
}