.ACC_DK_test {
    background-color: white;
    color: black;

    display: flex;
    align-items: center;
    justify-content: center;

    /* background-color: aqua; */

    height: 100vh;
    /* height: 0; */
    width: 100%;
    transition: all 1s ease;
}

.ACC_DK_test p {
    background-color: white;
    /* background-color: orange; */
    color: black;
    font-size: 3em;
    /* height: 0; */
    font-weight: bold;
    text-align: center;
}

.ACC_DK_test2 {
    background-color: white;
    color: black;

    display: flex;
    align-items: center;
    justify-content: center;

    height: 0;
    /* background-color: aqua; */

    /* height: 0; */


    animation: clignote2 1.5s ease-in-out;
}

@keyframes clignote2 {
    0% {
        height: 100vh;
    }

    100% {
        height: 0;
        font-size: 0;
    }
}

.ACC_DK_test2 p {
    background-color: white;
    /* background-color: orange; */
    color: black;
    font-weight: bold;
    text-align: center;
    /* height: 0; */
    /* font-size: 0; */
    
    animation: clignote3 1.5s ease-in-out;
}


@keyframes clignote3 {
    0% {
        font-size: 3em;
    }
    50% {
        font-size: 3em;
    }

    100% {
        font-size: 0;
    }
}