/* carroussel css */

.CSP_DK_carrousel_container {
    /* background-color: rgb(33, 211, 57); */
    height: 100%;
    width: 80%;
    overflow: hidden;
    margin: 0 auto 15px auto;
    padding: 0px;
}

.CSP_DK_carrousel_part {
    width: 33.333%;
    height: 100%;
    display: inline-block;
    background-size: cover;
    background-position: center;
    /* background-color: white; */
    /* background-color: red; */
}

.CSP_DK_carrousel_slideimage_wrap {
    /* background-color: yellow; */
    height: 100%;
    /* height: 0; */
    width: 90%;
    margin: auto;
    /* object-fit: contain; */
}

.CSP_DK_carrousel_slideimage {
    width: 100%;
    margin: auto;
    height: 100%;
    object-fit: cover;
    box-sizing: border-box;
    border: 1px solid black;
}

.CSP_DK_btn_wrap {
    /* background-color: aqua; */
    height: 0;
    /* height: 5px; */
    width: 98%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}

.CSP_DK_btn {
    background-color: white;
    /* background-color: orange; */
    border: none;
    font-size: 1.6em;
    height: 30px;
    position: relative;
    top: -95px;
    border-radius: 10px;
    display: flex;
    /* align-items: flex-end; */
    z-index: 1;
    /* border: 1px solid black; */
}

.CSP_DK_btn:hover {
    background-color: black;
    color: white;
    /* border: 1px solid white; */
}